import React from "react";

const HeaderShowSearch = ({ search }) => {
  return (
    <div className="lg:w-[720px] w-screen md:w-[460px] bg-white min-h-[40px] absolute top-[40px] lg:left-0 md:left-0 -left-7 rounded shadow cursor-pointer z-[99]">
      {search.length > 0 ? (
        <div className="flex items-center h-full p-3 bg-whiteSmoke gap-x-2 text-sm text-[#5f5f5f]">
          <IconShop></IconShop>
          <span>Tìm Shop {`"${search}"`}</span>
        </div>
      ) : (
        <div className="flex flex-col py-3 uppercase select-none text-sm text-[#5f5f5f]">
          <span className="px-4 mb-2 ">Siêu sale 9.9</span>
          <span className="px-4 mb-2">Đồ chơi</span>
          <span className="px-4 mb-2 ">Sách kinh doanh</span>
          <span className="px-4 ">Quần áo</span>
        </div>
      )}
    </div>
  );
};

