import { IconSearch } from "components/icons";
import useClickOutside from "hooks/useClickOutSide";
import React, { useState } from "react";
import HeaderShowSearch from "./HeaderShowSearch";

const HeaderSearch = () => {
  const { show, setShow, nodeRef } = useClickOutside();
  const [search, setSearch] = useState("");

  return (
    <div
      className="bg-white lg:w-[800px] w-[250px] md:w-[460px] flex items-center rounded py-1 z-50"
      ref={nodeRef}
    >
      <div className="relative">
        <input
          type="text"
          placeholder="Bạn muốn mua gì ?"
          className="px-4 text-sm lg:w-[720px] md:w-[380px] w-[180px] lg:mr-2 md:mr-5 placeholder:text-sm rounded mr-4"
          onClick={() => setShow(true)}
          onChange={(e) => setSearch(e.target.value)}
        />

        {show && <HeaderShowSearch search={search}></HeaderShowSearch>}
      </div>
      <button className="py-[8px] lg:px-6 px-3 md:px-4 text-white bg-primary rounded">
        <IconSearch></IconSearch>
      </button>
    </div>
  );
};

export default HeaderSearch;
