import React from "react";

const HeaderQrCode = () => {
  return (
    <div className={`bg-white w-[180px] p-2 h-[200px] transition-all header-qrCode`}>
      <img src="./qr_code.png" alt="Qr_Code" />
      <div className="flex gap-x-2">
        <img src="./app_store.png" alt="App_store" className="w-[80px]" />
        <img src="./google_play.png" alt="GooglePlay" className="w-[80px]" />
      </div>
    </div>
  );
};

export default HeaderQrCode;
