import { useAuth } from "contexts/auth-context";
import React from "react";
import { Link } from "react-router-dom";

const HeaderNotify = () => {
  const { userInfo } = useAuth();
  return (
    <div className="notify-pseudo bg-white w-[410px] h-[310px] absolute top-7 right-0  transition-all z-[51] shadow-md">
      <div className="flex flex-col items-center  text-[#000] ">
        <div className="flex flex-col items-center justify-center h-[270px]">
          <img
            src="./imgUser.png"
            alt="notify-User"
            className="w-[100px] h-[100px]"
          />
          <p className="text-[14px] mt-5 font-light">
            {userInfo ? "Không có thông báo" : "Đăng nhập để xem thông báo"}
          </p>
        </div>

        {userInfo ? (
          <div></div>
        ) : (
          <div className="flex h-[40px] w-full gap-x-5 bg-[#e8e8e8]">
            <Link to="/sign-up">
              <div className="flex items-center w-[190px] justify-center h-full p-2 hover:text-[#ee4d2d]">
                Đăng ký
              </div>
            </Link>
            <Link to="/sign-in">
              <div className="flex items-center w-[190px] justify-center h-full p-2 hover:text-[#ee4d2d] ">
                Đăng nhập
              </div>
            </Link>
          </div>
        )}
      </div>
    </div>
  );
};

export default HeaderNotify;
