import { IconCart } from "components/icons";
import { useAuth } from "contexts/auth-context";
import { useShopee } from "contexts/shopeeContext";
import useHover from "hooks/useHover";
import React from "react";
import { Link } from "react-router-dom";
import Cart from "./Cart";
import CartAmount from "./CartAmount";

const HeaderCart = () => {
  const {
    hover: showHeaderCart,
    handleShow: handleShowHeaderCart,
    handleHide: handleHideHeaderCart,
  } = useHover();
  const { cartItems } = useShopee();
  const { userInfo } = useAuth();
  return (
    <>
      <Link to="/payment">
        <div
          className="header-cart"
          onMouseOver={handleShowHeaderCart}
          onMouseOut={handleHideHeaderCart}
        >
          <div className="relative">
            <IconCart></IconCart>
            {userInfo?.email && <CartAmount></CartAmount>}
          </div>
          <div className="hidden lg:block">
            {showHeaderCart && <Cart></Cart>}
          </div>
        </div>
      </Link>
    </>
  );
};

export default HeaderCart;
