import { useAuth } from "contexts/auth-context";
import { useShopee } from "contexts/shopeeContext";
import React from "react";
import { Fragment } from "react";
import { Link } from "react-router-dom";
import { v4 } from "uuid";

const Cart = () => {
  const { cartItems } = useShopee();
  const { userInfo } = useAuth();

  return (
    <>
       {userInfo?.email ? (
        <Fragment>
          {cartItems.length > 0 ? (
            <div className="cart-pseudo absolute top-[48px] right-[-4px] bg-white w-[400px] h-[260px] cursor-text shadow-lg overflow-x-hidden overflow-y-auto">
              <div className="w-full h-full py-4 cursor-pointer">
                <h3 className="px-4 text-sm text-primary ">
                  Sản phẩm mới thêm
                </h3>
                {cartItems.length > 0 &&
                  cartItems.map((item) => (
                    <Link to="/payment" key={v4()}>
                      <CartItem data={item}></CartItem>
                    </Link>
                  ))}
                  <div className="pb-5"></div>
              </div>
             
            </div>
          ) : (
            <div className="cart-pseudo absolute top-10 right-[-4px] bg-white w-[400px] h-[260px] cursor-text shadow-lg ">
              <div className="flex flex-col items-center justify-center h-full ">
                <img
                  src="./cartEmpty.png"
                  alt="Cart-Empty"
                  className="w-[100px] h-[100px]"
                />
                <p className="mt-4 text-sm capitalize text-textColor ">
                  Chưa có sản phẩm
                </p>
              </div>
            </div>
          )}
        </Fragment>
      ) : (
        <Fragment>
          <div className="cart-pseudo absolute top-10 right-0 bg-white w-[400px] h-[260px] cursor-text shadow-lg ">
            <div className="flex flex-col items-center justify-center h-full ">
              <img
                src="./cartEmpty.png"
                alt="Cart-Empty"
                className="w-[100px] h-[100px]"
              />
              <p className="mt-4 text-sm capitalize text-textColor ">
                Chưa có sản phẩm
              </p>
            </div>
          </div>
        </Fragment>
      )} 
     
    </>
  );
};

function CartItem({ data }) {
  return (
    <div className="flex items-center mt-3 gap-x-3">
      <div className="ml-4 w-[60px] h-[60px] border border-gray-300 flex items-center justify-center">
        <img
          className="object-center w-[50px] h-[50px]"
          src={data.img_url}
          alt={data.title}
        />
      </div>
      <h3 className="w-[220px] text-sm text-limit-one-line">{data.title}</h3>
      <div className="text-primary">
        <span className="text-xs underline">đ</span> {data.price}
      </div>
    </div>
  );
}

export default Cart;
