import React from "react";
import Vertical from "components/common/Vertical";
import { IconArrowDown, IconGlobal, IconQuestion } from "components/icons";
import useHover from "hooks/useHover";

import HeaderLanguage from "./HeaderLanguage";
import { Link } from "react-router-dom";
import InfoUser from "components/userInfo/InfoUser";
import { useAuth } from "contexts/auth-context";
import Notify from "components/notify/Notify";

const HeaderTopRight = () => {
  const {
    hover: showLanguage,
    handleShow: handleShowLanguage,
    handleHide: handleHideLanguage,
  } = useHover();

  const { userInfo } = useAuth();

  return (
    <div className="flex items-center justify-center gap-x-3 text-[13px] font-normal text-white leading-none cursor-pointer select-none">
      <Notify></Notify>
      <div className="items-center justify-start hidden transition-all hover:opacity-70 gap-x-1 lg:flex">
        <IconQuestion></IconQuestion>
        Hỗ Trợ
      </div>
      <div
        className="language"
        onMouseOver={handleShowLanguage}
        onMouseOut={handleHideLanguage}
      >
        <span className="items-center justify-center hidden hover:opacity-70-70 gap-x-1 lg:flex">
          <IconGlobal></IconGlobal>
          Tiếng Việt
          <IconArrowDown></IconArrowDown>
          {showLanguage && <HeaderLanguage></HeaderLanguage>}
        </span>
      </div>

      <div>
        {userInfo ? (
          <InfoUser displayname={userInfo?.name}></InfoUser>
        ) : (
          <div className="flex gap-x-3">
            <Link to="/sign-up" className="transition-all hover:opacity-70">
              Đăng Ký
            </Link>
            <Vertical></Vertical>
            <Link to="/sign-in" className="transition-all hover:opacity-70">
              Đăng Nhập
            </Link>
          </div>
        )}
      </div>
    </div>
  );
};

export default HeaderTopRight;
