import { IconLogoShopee } from "components/icons";
import { Fragment } from "react";
import HeaderResponsive from "./HeaderResponsive";
import HeaderCart from "./parts/HeaderCart";
import HeaderSearch from "./parts/HeaderSearch";
import HeaderTop from "./parts/HeaderTop";

const Header = ({ className1, className2, hide = true }) => {
  return (
    <Fragment>
      <div
        className={`sticky top-0 bottom-0 left-0 right-0 py-2 header-bg z-[99] hidden lg:block  ${className1}`}
      >
        <div className="max-w-[1280px] mx-auto mb-4">
          <HeaderTop></HeaderTop>
          <div className={`flex items-center mt-6 gap-x-[70px] ${className2}`}>
            <IconLogoShopee fill="#fff"></IconLogoShopee>
            <HeaderSearch></HeaderSearch>
            {hide && <HeaderCart></HeaderCart>}
          </div>
        </div>
      </div>
      <HeaderResponsive></HeaderResponsive>
    </Fragment>
  );
};

export default Header;
