import React from "react";
import Vertical from "components/common/Vertical";
import { IconFacebook, IconInstagram } from "components/icons";
import useHover from "hooks/useHover";
import HeaderQrCode from "./HeaderQrCode";

const HeaderTopLeft = () => {
  const {
    hover: showQrCode,
    handleShow: handleShowQr,
    handleHide: handleHideQr,
  } = useHover();

  return (
    <div className="hidden items-center justify-center gap-x-2 text-[13px] font-normal text-white leading-none cursor-pointer select-none lg:flex">
      <span className="transition-all hover:opacity-70">Kênh Người Bán</span>
      <Vertical></Vertical>
      <span className="transition-all hover:opacity-70">
        Trở thành Người bán Shopee
      </span>
      <Vertical></Vertical>
      <div
        className="show-QrCode"
        onMouseOver={handleShowQr}
        onMouseOut={handleHideQr}
      >
        <span className="hover:opacity-70">Tải ứng dụng</span>
        {showQrCode && (
          <span className="absolute left-0 z-[51] top-7">
            <HeaderQrCode></HeaderQrCode>
          </span>
        )}
      </div>
      <Vertical></Vertical>
      <span className="transition-all hover:opacity-70">Kết nối</span>
      <span>
        <IconFacebook></IconFacebook>
      </span>
      <span>
        <IconInstagram></IconInstagram>
      </span>
    </div>
  );
};

export default HeaderTopLeft;
