
import { IconBell } from "components/icons";
import useHover from "hooks/useHover";
import HeaderNotify from "modules/header/parts/HeaderNotify";
import React from "react";

const Notify = () => {
  const {
    hover: showNotify,
    handleShow: handleShowNotify,
    handleHide: handleHideNotify,
  } = useHover();
  return (
    <div
      className="notify"
      onMouseOver={handleShowNotify}
      onMouseOut={handleHideNotify}
    >
      <div className="items-center justify-center hidden transition-all gap-x-1 hover:opacity-70 lg:flex">
        <IconBell></IconBell>
        <span>Thông báo</span>
      </div>
      {showNotify && <HeaderNotify></HeaderNotify>}
    </div>
  );
};

export default Notify;
