import { useShopee } from "contexts/shopeeContext";
import React from "react";

const CartAmount = () => {
  const { cartItems } = useShopee();

  const total = cartItems.length;

  return (
    <div className="absolute top-[-4px] left-[20px] flex items-center justify-center w-5 h-5 text-sm bg-white rounded-3xl text-primary">
      {total}
    </div>
  );
};

export default CartAmount;
