import { IconLogoShopee } from "components/icons";
import React from "react";
import HeaderCart from "./parts/HeaderCart";
import HeaderSearch from "./parts/HeaderSearch";
import HeaderUser from "./parts/HeaderUser";

const HeaderResponsive = () => {
  return (
    <div
      className={`sticky top-0 bottom-0 left-0 right-0 py-2 header-bg z-[99] lg:hidden block `}
    >
      <div className="flex items-center px-5 py-2 gap-x-2 md:gap-x-5 sm:gap-x-3">
        <div className="">
          <IconLogoShopee fill="#fff"></IconLogoShopee>
        </div>
        <HeaderSearch></HeaderSearch>
        <HeaderCart></HeaderCart>

        <HeaderUser></HeaderUser>
      </div>
    </div>
  );
};

export default HeaderResponsive;
